import React from "react"
import "./about.scss"
import { graphql, withPrefix } from "gatsby"
import { Layout } from "../components/Layout"
import { SEO } from "../components/Seo"
import {
    IntroSection,
    DescriptionSection,
    SkillsSection,
    FreeSection,
    ContactSection,
} from "../components/AboutMe"

const AboutPage = ({ data }) => {
    var node = data.allFile.edges[0].node
    var url = withPrefix(node.publicURL)
    return (
        <Layout>
            <SEO title="About Dhruv" />
            <div className="about-me-new">
                <IntroSection resumeUrl={url} />
                <ContactSection />
                <DescriptionSection />
                <SkillsSection />
                <FreeSection />
            </div>
        </Layout>
    )
}

export default AboutPage

export const query = graphql`
    query DownloadQuery {
        allFile(filter: { extension: { eq: "pdf" } }) {
            edges {
                node {
                    publicURL
                }
            }
        }
    }
`
