import React from "react"
import { OutboundLink } from "gatsby-plugin-google-analytics"
import { GithubSvg } from '../Svg/Github';

import "./contact-section.scss"

const githubProfileLink = "https://github.com/dhruv004"
const linkedInProfileLink = "https://www.linkedin.com/in/dhruvdatt/"
const twitterProfileLink = "https://twitter.com/ddatt04"
const stackOverflowProfileLink =
    "https://stackoverflow.com/users/6002933/dhrdatt?tab=profile"

export const ContactSection = () => (
    <section className="contact-section">
        <h3>Reach out to me</h3>
        <p className="social-icons">
            <OutboundLink
                className="social-icon"
                href={githubProfileLink}
                target="_blank"
            >
                <GithubSvg />
            </OutboundLink>
            <OutboundLink
                className="social-icon"
                href={twitterProfileLink}
                target="_blank"
            >
                <img
                    src={require("../../static/twitter-original.svg")}
                    alt="twitter"
                    title="Twitter"
                ></img>
            </OutboundLink>
            <OutboundLink
                className="social-icon"
                href={stackOverflowProfileLink}
                target="_blank"
            >
                <img
                    src={require("../../static/stackoverflow-icon.svg")}
                    alt="stackoverflow"
                    title="StackOverflow"
                ></img>
            </OutboundLink>
            <OutboundLink
                className="social-icon"
                href={linkedInProfileLink}
                target="_blank"
            >
                <img
                    src={require("../../static/linkedin-original.svg")}
                    alt="LinkedIn"
                    title="LinkedIn"
                ></img>
            </OutboundLink>
        </p>
    </section>
)
